<template>
  <div>
    
    <CRow class="justify-content-center">
      <CCol md="6">
        <CCard>
          <CCardHeader >
            <strong >Edit Stimulasi</strong>
            
          </CCardHeader>
          <CCardBody>
            <CForm @submit.prevent="updateStimulasi" method="post">
              <CInput
                label="Embed Video"
                horizontal
                autocomplete="name"
                v-model="stimulasiUpdate.video_embed"
                required
              />
              
              
              <CButton type="submit" size="sm" color="primary" class="mr-2"><CIcon name="cil-check-circle"/> SAVE</CButton>
            </CForm>
          </CCardBody>
          
        </CCard>
        
      </CCol>
      
    </CRow>
    
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'EditStimulation',
  data(){
    return {
      stimulasiUpdate: {
        stimulasi_judul: '',
        image_name: '',
        video_embed: '',
      },
      stimulations: []
    }
  },
  methods: {
    setStimulation(data){
      this.stimulations = data;
    },
    updateStimulasi(data){
      this.stimulations = data;

      let body = {
        admin_user_id : localStorage.getItem('admin_id'),
        stimulasi_judul: this.stimulasiUpdate.stimulasi_judul,
        image_name: this.stimulasiUpdate.image_name,
        video_embed: this.stimulasiUpdate.video_embed,
      };

      const result = 
        axios
        .post('http://103.151.215.74/api/admin/stimulasi/detail/update/' + localStorage.getItem('stimulasiId') , body, {
          headers: {
            Authorization: localStorage.getItem('token')
          }
        })
        .then(
          (result) => {
            // console.log("Berhasil : ", result);
            localStorage.removeItem('stimulasiId');
            this.$router.push('/menus/stimulasi');
          }
        )
        .catch(function (error) {
          // handle error
          // console.log("Gagal : ", error);
        });
    }
  },

  mounted() {
    let body = {
      admin_user_id : localStorage.getItem('admin_id')
    };

    axios
      .post('http://103.151.215.74/api/admin/stimulasi/detail/edit/' + localStorage.getItem('stimulasiId') , body, {
        headers: {
          Authorization: localStorage.getItem('token')
        }
      })
      .then(
        // (response) => this.setThread(response.data.json_data)
        (response) => {
          // console.log("Berhasil : ", response);
          this.setStimulation(response.data.json_data);
        }
        
      )
      // .catch(function (error) {
      //   // handle error
      //   // console.log("Gagal : ", error);
      // })
  },
  
}
</script>